<template>
  <div class="purchase-now">
    <!--<shop-header style="z-index: 999;background-color: #fff"-->
    <!--title="提交订单"></shop-header>-->
    <scroll class="purchase-scroll">
      <div>
        <purchase-address @toSetAddress="toSetAddress"
                          :address-info="addressInfo"></purchase-address>
        <store class="store" :show-change="false" @minus="minus"
               @add="add"
               :data="shopList"></store>
        <remark v-model="remark"></remark>
        <purchase-info :return-tb="returnTb" :price="price"></purchase-info>
      </div>
    </scroll>
    <save-bar :save-price="savePrice"></save-bar>
    <purchase-bar @close="close"
                  :price="price"
    ></purchase-bar>
    <load-server v-show="showLoad"></load-server>
  </div>
</template>

<script>
  import tool from '../../common/js/util'
  import ShopHeader from '../../base/shop-header/shop-header'
  import PurchaseAddress from '../../base/purchase-address/purchase-address'
  import Store from '../../base/store/store'
  import Scroll from '../../base/scroll/scroll'
  import PurchaseInfo from '../../base/purchase-info/purchase-info'
  import LoadServer from '../../base/load-server/load-server'
  import PurchaseBar from '../../base/purchase-bar/purchase-bar'
  import Remark from '../../base/remark/remark'
  import SaveBar from '../../base/save-bar/save-bar'
  import * as core from '../../api/purchaseNow'

  export default {
    name: 'purchase-now',
    data() {
      return {
        addressInfo: null,
        shopList: [],
        showLoad: false,
        price: '',
        remark: '',
        returnTb: '',
        savePrice: 0
      }
    },
    components: {
      ShopHeader,
      Scroll,
      Store,
      PurchaseAddress,
      LoadServer,
      PurchaseBar,
      Remark,
      PurchaseInfo,
      SaveBar
    },
    created() {
      var u = navigator.userAgent, app = navigator.appVersion;
      var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
      var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
      // function IOSInput() {
        if (isIOS) {
          var itimer;
          document.body.addEventListener('focusin', function () {
            setTimeout(function () {
              clearTimeout(itimer);
            }, 200);
          })
          document.body.addEventListener('focusout', function () {
            itimer = setTimeout(function () {
              window.scrollTo(0,0)
            }, 300);
          })
        }
      // }
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      if (this.$route.query.cartIds) {
        this.getPurchaseNow({cartIds: this.$route.query.cartIds})
      } else {
        this.getPurchase({
          sku: this.$route.query.sku,
          num: this.$route.query.num
        })
      }
      this.getUserAddress()
    },
    methods: {
      getPurchase(opts) {
        core.purchase(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.shopList.push({})
            this.shopList[0].title = res.result.skuItem.title
            this.shopList[0].specValueStr = res.result.skuItem.specValueStr
            this.shopList[0].singleMinPayTb = res.result.skuItem.minPayTb
            this.shopList[0].singlePrice = res.result.skuItem.price
            this.shopList[0].oldPrice = res.result.skuItem.oldPrice
            this.shopList[0].num = res.result.count
            this.shopList[0].cover = res.result.skuItem.cover
            this.shopList[0].singleOldPrice = res.result.skuItem.oldPrice
            this.shopList[0].singleCashPrice = res.result.skuItem.cashPrice
            this.shopList[0].returnTbRate = res.result.returnTbRate

            let price = 0
            let priceTb = 0
            let savePrice = 0

            for (var i = 0; i < this.shopList.length; i++) {
              savePrice = savePrice + (this.shopList[i].singleOldPrice - this.shopList[i].singlePrice) * this.shopList[i].num
              price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
              priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num

            }
            this.returnTb = (price * 100 * Number(res.result.returnTbRate) / 100).toFixed(0)
            this.price = tool.priceStr(priceTb, price)
            this.savePrice = savePrice
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      close() {

        if (!this.addressId) {
          this.$toastBox.showToastBox('请选择收货地址！')
          return
        }
        if (!this.serverState){
          if (this.$route.query.cartIds) {
            this.serverState=true
            core.cartOrder({remark:this.remark,cartIds:this.$route.query.cartIds,userAddressId:this.addressId}).then(res=>{
              if (res.code&&'00'===res.code){
                window.location.href=res.result.payUrl
              }else if (res.code && '01' === res.code) {
                tool.toWeiXinLogin(window.location.href)
              } else {
                this.serverState=false
                this.$toastBox.showToastBox(res.message)
              }
            }).catch(e => {
              this.serverState=false
              this.$toastBox.showToastBox(e)
            })
          } else {
            this.serverState=true
            core.skuOrder({    sku:this.$route.query.sku,
              count:this.shopList[0].num,
              userAddressId:this.addressId,
              remark:this.remark}).then(res=>{
              if (res.code&&'00'===res.code){
                window.location.href=res.result.payUrl
              }else if (res.code && '01' === res.code) {
                tool.toWeiXinLogin(window.location.href)
              } else {
                this.serverState=false
                this.$toastBox.showToastBox(res.message)
              }
            }).catch(e => {
              this.serverState=false
              this.$toastBox.showToastBox(e)
            })
          }
        }

        console.log()
      },
      getUpNum(opts, isAdd, index) {
        this.showLoad = true;
        core.upNum(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.showLoad = false
            if (isAdd) {
              this.shopList[index].num = this.shopList[index].num + 1
            } else {
              this.shopList[index].num = this.shopList[index].num - 1
            }
            let price = 0
            let priceTb = 0
            let savePrice = 0

            for (var i = 0; i < this.shopList.length; i++) {
              savePrice = savePrice + (this.shopList[i].singleOldPrice - this.shopList[i].singlePrice) * this.shopList[i].num;
              price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num;
              priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num

            }
            this.returnTb = (price * 100 * Number(res.result.returnTbRate) / 100).toFixed(0)
            this.price = tool.priceStr(priceTb, price)
            this.savePrice = savePrice
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      minus(index) {
        if (this.showLoad) {
          return false
        }
        if (this.shopList[index].num <= 1) {
          this.$toastBox.showToastBox('购买数量不能小于1')
          return false
        }
        if (this.$route.query.cartIds) {
          this.getUpNum({
            sku: this.shopList[index].sku,
            num: this.shopList[index].num - 1
          }, false, index)
        } else {
          this.shopList[index].num = this.shopList[index].num - 1
          let price = 0
          let priceTb = 0
          let savePrice = 0

          for (var i = 0; i < this.shopList.length; i++) {
            savePrice = savePrice + (this.shopList[i].singleOldPrice - this.shopList[i].singlePrice) * this.shopList[i].num
            price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
            priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num

          }
          this.returnTb = (price * 100 * Number(this.shopList[0].returnTbRate) / 100).toFixed(0)
          this.price = tool.priceStr(priceTb, price)
          this.savePrice = savePrice
        }


      },
      add(index) {
        if (this.showLoad) {
          return false
        }
        if (this.$route.query.cartIds) {
          this.getUpNum({
            sku: this.shopList[index].sku,
            num: this.shopList[index].num + 1
          }, true, index)
        } else {
          this.shopList[index].num = this.shopList[index].num + 1
          let price = 0
          let priceTb = 0
          let savePrice = 0

          for (var i = 0; i < this.shopList.length; i++) {
            savePrice = savePrice + (this.shopList[i].singleOldPrice - this.shopList[i].singlePrice) * this.shopList[i].num
            price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
            priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num

          }
          this.returnTb = (price * 100 * Number(this.shopList[0].returnTbRate) / 100).toFixed(0)
          this.price = tool.priceStr(priceTb, price)
          this.savePrice = savePrice
        }

      },
      toSetAddress() {
        let qua = {}
        if (this.$route.query.cartIds){
          qua.cartIds=this.$route.query.cartIds
        }
        if (this.$route.query.sku){
          qua.sku=this.$route.query.sku
        }
        if (this.$route.query.num){
          qua.num=this.$route.query.num
        }
        this.$router.push({path: '/addressList',query:qua})
      },
      getUserAddress() {
        core.userAddress().then(res => {
          if (res.code && '00' === res.code) {
          if (this.$route.query.addressId){
            for (var i = 0; i < res.result.length; i++) {
              if (res.result[i].id == this.$route.query.addressId) {
                this.addressId = res.result[i].id
                this.addressInfo = res.result[i]
                console.log(this.addressInfo)
              }
            }
          } else {

            for (var i = 0; i < res.result.length; i++) {
              if (res.result[i].isDefault === 'Y') {
                this.addressId = res.result[i].id
                this.addressInfo = res.result[i]
                console.log(this.addressInfo)
              }
            }
          }

          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      getPurchaseNow(opts) {
        core.purchaseNow(opts).then(res => {
          if (res.code && '00' === res.code) {
            // this.returnTbRate=res.result.returnTbRate
            if ( res.result.cartList.length===0){
              this.$router.push({path:'/'})
            }
            this.shopList = res.result.cartList;
            let price = 0
            let priceTb = 0
            let savePrice = 0
            for (var i = 0; i < this.shopList.length; i++) {
              savePrice = savePrice + (this.shopList[i].singleOldPrice - this.shopList[i].singlePrice) * this.shopList[i].num
              price = price + (this.shopList[i].singleCashPrice) * this.shopList[i].num
              priceTb = priceTb + (this.shopList[i].singleMinPayTb) * this.shopList[i].num
            }
            this.returnTb = (price * 100 * Number(res.result.returnTbRate) / 100).toFixed(0)
            this.price = tool.priceStr(priceTb, price)
            this.savePrice = savePrice
          }else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      }
    },
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .purchase-now
    .purchase-scroll
      position fixed
      /*top 2.75rem*/
      top 0
      left 0
      right 0
      background-color #f5f5f5
      bottom 5.313rem
      .store
        margin-top 0.625rem
</style>



