<template>
  <div class="purchase-bar">
    <div class="num">实付款: <span>{{price}}</span></div>
    <div @click="close" class="delet">立即购买</div>
  </div>
</template>

<script>
  export default {
    name: 'delet-bar',
    props: {
      num: {
        type: Number,
        default: 0
      },
      price:{
        type:String,
        default:''
      }
    },
    methods:{
      changeAll(){
        this.$emit('changeAll')
      },
      close(){
        this.$emit('close')
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .purchase-bar
    border-top 1px solid #e6e6e6
    position fixed
    height 3.063rem
    width 100%
    bottom 0
    left 0
    z-index 1000
    background-color #fff
    box-sizing border-box
    line-height 3.063rem
    display flex
    justify-content space-between
    padding 0 0 0 1rem
    align-items center
    .num
      font-size 0.813rem
      span
       color #ff4800
      color #333
    .delet
      width: 5.938rem;
      height 100%
      line-height 3.063rem
      text-align center
      font-size 1rem
      background-color: #ff4800;
      color #fff
</style>



