<template>
  <div class="purchase-info">
    <div class="wrapper">
      <div class="return-tb">

        <span><i class="iconfont tb-icon">&#xe630;</i>&nbsp;&nbsp;最多可返还通宝</span>
        <span>{{returnTb}}通宝</span>
      </div>
      <div class="price">
        <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;商品金额</span>
        <span>{{price}}</span>
      </div>
      <div class="yunfei">
        <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;运费(袋鼠自营商城)</span>
        <span>+￥0.00</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'purchase-info',
    props:{
      price:{
        type:String,
        default:''
      },
      returnTb:{
        type:String,
        default:""
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .purchase-info
    padding-bottom 1rem
    .wrapper
      margin-top 0.625rem
      width 100%
      padding 0 4.27%
      font-size 0.813rem
      box-sizing border-box
      background-color #fff
      color #333
      div
          display flex
          justify-content space-between
          span
            line-height 2rem
        .return-tb
          span
            line-height 2.75rem
            i
              color #ff4800
              vertical-align middle
          border-bottom 1px solid #e6e6e6
</style>



