import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'

/*
* 根据
* */
export function purchaseNow(opts) {
  return axios.post(CONFIG.API.PURCHASE_NOW, qs.stringify({
    cartIds:opts.cartIds
  })).then(res => res.data)
}

/*
* 根据
* */
export function userAddress() {
  return axios.post(CONFIG.API.USER_ADDRESS).then(res => res.data)
}

/*
* 根据
* */
export function upNum(opts) {
  return axios.post(CONFIG.API.UP_NUM, qs.stringify({
    sku:opts.sku,
    num:opts.num
  })).then(res => res.data)
}

/*
* 根据
* */
export function purchase(opts) {
  return axios.post(CONFIG.API.PURCHASE, qs.stringify({
    sku:opts.sku,
    num:opts.num
  })).then(res => res.data)
}


/*
* 根据
* */
export function cartOrder(opts) {
  return axios.post(CONFIG.API.CART_ORDER, qs.stringify({
    remark:opts.remark,
    cartIds:opts.cartIds,
    userAddressId:opts.userAddressId
  })).then(res => res.data)
}

/*
* 根据
* */
export function skuOrder(opts) {
  return axios.post(CONFIG.API.SKU_ORDER, qs.stringify({
    sku:opts.sku,
    count:opts.count,
    userAddressId:opts.userAddressId,
    remark:opts.remark
  })).then(res => res.data)
}



