<template>
  <div class="purchase-address" @click="toSetAddress">
    <div class="purchase-inner">
      <div class="no-default" v-if="!addressInfo">
        <i class="iconfont">&#xe63b;</i>
        请选择收货地址
        <i class="iconfont to-address">&#xe624;</i>
      </div>
      <div class="default" v-if="addressInfo">
        <div class="top">
          <i class="iconfont">&#xe63b;</i>
          <span>{{addressInfo.realname}}</span>&nbsp;&nbsp;
          <span>{{addressInfo.mobile}}</span>
        </div>
        <div class="bottom">
          {{addressInfo.province+addressInfo.city+addressInfo.district+addressInfo.town+addressInfo.address}}
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'purchase-address',
    props: {
      addressInfo: {
        type: Object,
        default: null
      }
    },
    methods:{
      toSetAddress(){
        this.$emit('toSetAddress')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .purchase-address
    font-size 0.938rem
    color #fff
    background-color #ff4450
    .purchase-inner
      padding 0 4.27%
      .no-default
        height 3.063rem
        line-height 3.063rem
        .to-address
          position absolute
          right 4.27%
      .default
        line-height 1.343rem
        .top
          padding-top 1.125rem
        .bottom
          padding-bottom 0.8rem
          padding-left 1.25rem
          font-size 0.813rem
          letter-spacing 1px
</style>



