<template>
    <div class="remark">
      <label>备注：</label>
      <input @click="focusRemark" maxlength="30" @input="$emit('input', $event.target.value)" placeholder="买家留言(最多30字)"/>
    </div>
</template>

<script>
  export default {
    name: 'remark',
    methods:{
      focusRemark(e){
        e.target.focus()
      }
    },
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .remark
    label
      position absolute
      left 4.27%
      font-size 0.813rem
      color #333
      line-height 2rem
    width 100%
    padding 1.188rem 4.27%
    background-color #fff
    margin-top 0.625rem
    input
      -webkit-user-select : text
      font-size 0.813rem
      color #333
      outline none
      height 2rem
      width 78%
      border none
      padding 0
      margin-left 2.5rem
      &::placeholder
        font-size 0.813rem
</style>



