<template>
    <div class="save-bar">
      <span>积分袋鼠已为你节省</span>
      <span>￥{{savePrice.toFixed(2)}}</span>
    </div>
</template>

<script>
  export default {
    name: 'save-bar',
    props:{
      savePrice:{
        type:Number,
        default:0
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.save-bar
  position fixed
  left 0
  display flex
  justify-content space-between
  padding 0 4.27%
  bottom 3.063rem
  line-height 2.25rem
  box-sizing border-box
  font-size 0.813rem
  height 2.25rem
  width 100%
  background-color #fff5dd
</style>



